.main-button-4-start-menu {
  align-items: center;
  border: 0.25px solid;
  border-color: var(--black);
  border-radius: 22px;
  height: 20px;
  justify-content: center;
  min-width: 90px;
  opacity: 0;
  transform: scale(0);
}

.main-button-4-start-menu.animate-enter4 {
  animation: animate-enter4-frames 0.60s ease-in-out 0.30s 1 normal forwards;
  opacity: 0;
  transform: scale(0);
  text-decorations:none
}

.about-4 {
  height: 15px;
  letter-spacing: 0;
  line-height: normal;
  margin-left: 18px;
  margin-top: 0px;
  text-align: center;
  width: 48px;
  text-decorations:none
}

@keyframes animate-enter4-frames {
  from{opacity: 0;
transform: scale(0);
}
to{opacity: 1;
transform: scale(1);
}
}

a:visited {
  color: black;
}