.main-button-2-start-menu {
  align-items: center;
  border: 0.25px solid;
  border-color: var(--black);
  border-radius: 22px;
  height: 20px;
  justify-content: center;
  min-width: 90px;
  opacity: 0;
  transform: scale(0);
}

.main-button-2-start-menu.animate-enter2 {
  animation: animate-enter2-frames 0.60s ease-in-out 0.30s 1 normal forwards;
  opacity: 0;
  transform: scale(0);
}

.about-2 {
  height: 15px;
  letter-spacing: 0;
  line-height: normal;
  margin-left: 11px;
  margin-top: 0px;
  text-align: center;
  width: 52px;
}

@keyframes animate-enter2-frames {
  from{opacity: 0;
transform: scale(0);
}
to{opacity: 1;
transform: scale(1);
}
}

