.title-welcome-screen {
  cursor: pointer;
  display: flex;
  height: 40px;
  margin-top: -18.50px;
  min-width: 280px;
  transition: all 0.5s ease-in-out;
}

.title-welcome-screen:hover {
  transform: translate(5px, 0);
}

.title-1 {
  color: transparent;
  flex: 1;
  font-family: var(--font-family-suisse_intl-medium);
  font-size: var(--font-size-s);
  font-weight: 500;
  letter-spacing: 0;
  line-height: normal;
  width: 280px;
}

.span0 {
  color: var(--black);
}

.span1 {
  color: #dcdddd;
}

