
@import url("https://cdnjs.cloudflare.com/ajax/libs/meyer-reset/2.0/reset.min.css");
/* The following line is used to measure usage of this code in production. For more info see our usage billing page */
@import url("https://px.animaapp.com/6373c51f1be4ff5592b6817a.6373c5201be4ff5592b6817d.iA91bna.rcp.png");

@font-face {
  font-family: "Suisse Intl-Regular";
  font-style: normal;
  font-weight: 400;
  src: url("static/fonts/SuisseIntl-Regular.otf") format("opentype");
}
@font-face {
  font-family: "Suisse Intl-Medium";
  font-style: normal;
  font-weight: 500;
  src: url("static/fonts/SuisseIntl-Medium.otf") format("opentype");
}

a:visited {
  color: black;
}


.screen a {
  display: contents;
  text-decoration: none;
}

.valign-text-middle {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.fade-in {
  transition: opacity 1s ease;
}

.fade-out {
  opacity: 0;
  transition: opacity 1s ease;
}

* {
  box-sizing: border-box;
}


.fadeTranslate-enter {
  opacity: 0;
  transform: translate(0, -3vh);
  position: fixed;
}

.fadeTranslate-enter.fadeTranslate-enter-active {
  opacity: 1;
  transform: translate(0, 0);
  transition: opacity 500ms ease-in 500ms, transform 500ms ease-in-out 500ms;
}

.fadeTranslate-exit {
  opacity: 1;
  position: fixed;
  transform: translate(0, 0);
}

.fadeTranslate-exit.fadeTranslate-exit-active {
  opacity: 0;
  transform: translate(0, 3vh);
  transition: opacity 300ms ease-in, transform 300ms ease-in-out;
}

.fix-container {
  position: fixed;
}